.Card {
  margin: auto auto;
  width: 240px;
  height: 240px;
  border-radius: 50%;
  background-color: white;
  padding: auto auto;
  box-shadow: 1px 2px 4px 1px rgb(71, 71, 71);
}

.Card button {
  width: 100%;
  height: 100%;
  background-color: inherit;
  border-radius: 50%;
  border: none;
  transition: background-color 0.2s;
}

.Card svg {
  margin-left: 12px;
  color: rgb(4, 182, 28);
  transition: transform 0.2s, color 0.2s;
}

.Card button:hover svg {
  transform: scale(0.85);
  color: white;
}

.Card button:hover {
  background-color: rgb(0, 196, 26);
}

.Card img {
  width: 240px;
  border-radius: 50%;
}

.NextCard {
  width: 240px;
  display: none;
}

.Close {
  animation-name: closeCard;
  animation-duration: 0.3s;
}

@keyframes closeCard {
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(180deg);
    opacity: 0;
  }
  100% {
    transform: rotateY(0deg);
    opacity: 1;
  }
}
